import React from 'react';
import { handleFieldChange } from "react-lib";
import DataInvalida from '../_shared/Data';
import  { UsuarioService } from 'prevsystem-service';

var InputMask = require('react-input-mask');

var config = require('../../config.json');
var usuarioService = new UsuarioService(config);

export default class EsqueciSenha extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            cpf: "",
            dataNascimento: "",

            // States Validao
            erroCampoVazio: false,
            erroCampoInvalido: false,

            mensagemErro: ""
        }

        this.validarCampos = this.validarCampos.bind(this);
        this.validarVazios = this.validarVazios.bind(this);
        this.validarInvalidos = this.validarInvalidos.bind(this);
        this.converteData = this.converteData.bind(this);
        this.renderizaMensagemErro = this.renderizaMensagemErro.bind(this);
        this.enviarSenha = this.enviarSenha.bind(this);
    }
    
    componentDidMount() {
    }

    validarCampos() {
        this.validarVazios();
    }

    /**
     * Mtodo que checa se os campos esto vazios e atualiza o state que contm essa informao. Aps isso faz uma chamada de validarInvalidos().
     */
    validarVazios() {

        if(this.state.cpf === "" || this.state.dataNascimento === "") {
            this.setState({
                erroCampoVazio: true
            }, () => { this.validarInvalidos() });

        } else {
            this.setState({
                erroCampoVazio: false
            }, () => { this.validarInvalidos() });
        }

    }

    /**
     * Mtodo que valida os campos Cpf e DataNascimento. Para validao de Cpf, o valor deve conter um tamanho maior que 14. Para validao de senha  
     * utilizado uma funo que valida a data para no aceitar datas futuras e estar dentro dos limites de dias e meses. Os states so atualizados e
     * faz-se uma chamada ao renderizaMensagemErro().
     */
    validarInvalidos() {
        var cpfInvalido = false;
        if(this.state.cpf.length < 11)
            cpfInvalido = true;

        var dataObjeto = this.converteData(this.state.dataNascimento);
        var dataInvalida = DataInvalida(dataObjeto, this.state.dataNascimento);

        if(cpfInvalido || dataInvalida) {
            this.setState({ 
                erroCampoInvalido: true 
            }, () => { this.renderizaMensagemErro() });
        } else {
            this.setState({ 
                erroCampoInvalido: false 
            }, () => { this.renderizaMensagemErro() })
        }

        console.log(this.state);
    }
    
    /**
     * @param {string} dataString Data a ser convertida para Date().
     * @description Mtodo responsvel por converter a data recebida (no formato 'dd/mm/aaaa') para date (Objeto).
     */
    converteData(dataString) {

        var dataPartes = dataString.split("/");
        return new Date(dataPartes[2], dataPartes[1] - 1, dataPartes[0]);
    }

    /**
     * Mtodo que altera o state 'mensagemErro' para o tipo de mensagem que deve ser mostrada para o usurio. Aps isso faz uma chamada de enviarSenha.
     */
    renderizaMensagemErro() {

        if(this.state.erroCampoVazio) {
            this.setState({
                mensagemErro: "Preencha todos os campos!"
            })
        } else if(this.state.erroCampoInvalido) {
            this.setState({
                mensagemErro: "Preencha todos os campos corretamente!"
            })
        } else {
            this.setState({
                mensagemErro: ""
            }, () => { this.enviarSenha() })
        }

    }

    /**
     * Mtodo que chama o service que cria um POST com Cpf e DataNascimento para rota '/usuario/criarAcesso'. Aps isso, redireciona para tela de login.
     */
    enviarSenha() {
        var cpf = this.state.cpf;
        var dataNascimento = this.state.dataNascimento;

        usuarioService.PrimeiroAcesso(cpf, dataNascimento)
            .then((result) => {
                window.alert(result.data);
                this.props.history.push('/');
            })
            .catch((err) => { 
                this.setState({
                    mensagemErro: "Dados invlidos!"
                }, () => { 
                    console.error(err);
                })
            });
    }

    render() {
        return(
            <div className="box">
                <div className="logo">
                    <img src="./imagens/preves/logo.png" alt="Preves" />
                </div>

                <h4>Bem vindo ao portal da Preves</h4>

                <div className="box-content">
                    <p align="middle"><b>Esqueci minha senha / Primeiro Acesso</b></p>
                    <p align="middle">Preencha as informaes para que possamos gerar uma senha que ser enviada para seu email cadastrado.</p>
                    <form>
                        <div className="form-group">
                            <input name="cpf" placeholder="CPF (somente nmeros)" maxLength="14" className="form-control" value={this.state.cpf} onChange={(e) => handleFieldChange(this, e)} />
                        </div>

                        <div className="form-group">
                            <InputMask mask="99/99/9999" placeholder="Data de Nascimento" className="form-control"
                                       name="dataNascimento" value={this.state.dataNascimento} onChange={(e) => handleFieldChange(this, e)} />
                        </div>

                        {this.state.mensagemErro !== "" &&
                            <div className="text-danger">
                                {this.state.mensagemErro}
                            </div>
                        }
                        <br/>

                        <button className="btn btn-primary btn-block" type="button" onClick={this.validarCampos}>Enviar Nova Senha</button>
                        <button className="btn btn-default btn-block" type="button" onClick={() => document.location = "/"}>Cancelar</button>
                    </form>
                </div>
            </div>
        )
    }

}



